<template>
  <!-- 最外层大盒子 -->
  <div class="bx-myWorks-warp">
    <!-- 创建新作品 -->
    <div class="bx-myWorks-warp-makePro">
      <div class="bx-myWorks-warp-makePro-add">
        <i class="el-icon-plus"></i>
      </div>
      <div class="bx-myWorks-warp-makePro-text">创建新作品</div>
    </div>
    <VxMyWorks v-for="(item,index) in UserPicModel" :key="index" :item="item" />
  </div>
</template>

<script>
import VxMyWorks from 'src/components/userPicModel'
import { userCenterList } from 'src/api/api'

export default {
  name: 'vx-bxMyWorks',
  data() {
    return {
      UserPicModel: []
    }
  },
  components: { VxMyWorks },
  created() {
    const loading = this.$loading({
      lock: true,
      text: '正在加载...',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    })

    userCenterList().then(res => {
      this.UserPicModel.length = 0
      this.UserPicModel = [...res.data.list]
      loading.close()
    })
  }
}
</script>

<style scoped lang="scss">
.bx-myWorks-warp {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  min-width: 1200px;

  .bx-myWorks-warp-makePro {
    width: 304px;
    height: 450px;
    margin-left: 16px;
    background: white;
    position: relative;
    box-sizing: border-box;
    color: #7c7c7c;
    cursor: pointer;
    margin-bottom: 16px;

    .bx-myWorks-warp-makePro-add {
      height: 408px;
      line-height: 408px;
      text-align: center;
      font-size: 18px;
      cursor: pointer;
    }
    .bx-myWorks-warp-makePro-text {
      height: 42px;
      text-align: center;
      line-height: 42px;
      border-top: 1px solid #b7b7b7;
      border-width: thin;
    }
  }
}
</style>
